* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #1a1a1a;
  text-align: left;
  background-color: #fbfae1;
}

a:link,
a:visited {
  color: #3A0088;
  text-decoration: underline;
}

a:active,
a:hover {
  color: #5628B4;
  text-decoration: underline;
}

.root {
  max-width: 700px;
  margin: 50px auto;
  padding: 20px;
  background: #fff;
  border: 1px solid #1a1a1a;
  -webkit-box-shadow: 5px 5px 0 0 #1a1a1a;
          box-shadow: 5px 5px 0 0 #1a1a1a;
}

.emoji-toggle-container {
  color: #fff;
  text-align: right;
  padding: 20px;
  margin: -20px -20px 0 -20px;
  background: #3A0088;
}

.emoji-toggle-label {
  cursor: pointer;
}

h1 {
  margin-bottom: 10px;
}

.menu {
  margin-bottom: 20px;
}

.menu-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0;
  list-style-type: none;
}

.menu-list-item {
  text-transform: uppercase;
  margin-right: 15px;
}

.footer {
  text-align: center;
}



